.container {
  margin-top: 85px;
}

.header {
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
}

.header-text {
  font-weight: 700;
  font-size: 36px;
  line-height: 140%;
  letter-spacing: 0.05em;
  color: #ffffff;
}

/* .green-text {
  color: #59be99;
